<template>
  <b-container>
    <b-row>
      <b-col cols="12">
        <ProgressCircle :current-step="6" :total-steps="7"></ProgressCircle>
        <owner-info ref="owner" :isAddListing="true" />
        <div class="page-actions">
          <b-button @click="goBack">{{ $t('common.back') }}</b-button>
          <b-button variant="primary" @click="goNext">
            {{ $t('common.next') }}
          </b-button>
        </div>
      </b-col>
    </b-row>
  </b-container>
</template>

<script>
import ownerInfo from '@/components/dashboard/my-properties/owner-info.vue'
import ProgressCircle from '@/components/postingRequestForm/ProgressCircle';

export default {
  name: 'AddPropertyPage5',
  components: {
    ownerInfo,
    ProgressCircle,
  },
  data(){
    return {
      currentProp: null
    }
  },
  computed: {
    dir() {
      return this.$store.getters['CoreModule/isRTL'] ? 'rtl' : 'ltr';
    },
  },
  methods: {
    goNext() {
      localStorage.setItem(
        'property',
        JSON.stringify({
          ...this.currentProp,
          form6: this.$refs.owner.$data.ownerData,
        })
      );
      this.$router.push(this.localePath('/properties/add/7'));
    },
    goBack() {
      this.$router.push(this.localePath('/properties/add/5'));
    },
  },
  head() {
    return {
      title: `${this.dir === "ltr" ? "Add Property | Step 5" : "إضافة عقار | الخطوة 5"} ${this.appEnv !== 'production' ? '| Staging' : ''}`,
      meta: [
        // hid is used as unique identifier. Do not use `vmid` for it as it will not work
        {
          hid: 'description',
          name: 'description',
          content: this.dir === "ltr" ? 'Add Property | Property Images' : 'إضافة عقار | صور العقار'
        }
      ]
    }
  },
  mounted() {
    window.scrollTo({
      top: 0,
      left: 0,
      behavior: 'smooth',
    });
    const currentProperty = localStorage.getItem('property') && JSON.parse(localStorage.getItem('property'));
    this.currentProp = JSON.parse(localStorage.getItem('property'));
    if (!currentProperty)
      this.$router.push(this.localePath('/properties/add/1'));
  },
};
</script>

<style lang="scss">
.form-input {
  .container {
    .mainMessage {
      text-align: center;
      font-size: 16px;
      font-weight: bold;
      color: #0078ff;
    }
    .imgsPreview {
      .imageHolder {
        .plus {
          display: flex;
          align-items: center;
          justify-content: center;
        }
      }
    }
  }
}
.uploaded-images {
  padding: 16px 0;
  h3 {
    font-size: 16px;
    font-weight: bold;
    margin: 16px 0;
  }
  .row {
    .col-auto {
      img {
        position: relative;
        object-fit: cover;
        margin: 8px 0;
      }
      .delete-image {
        position: absolute;
        display: flex;
        justify-content: center;
        align-items: center;
        top: 10px;
        right: 16px;
        width: 32px;
        height: 32px;
        background: #ea293c;
        clip-path: circle();
        cursor: pointer;
      }
    }
  }
}
</style>
